import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';
import PlayButtonNormal from '../assets/icons/playbutton-normal.svg';
import PlayButtonHover from '../assets/icons/playbutton-hover.svg';
import { IPlaylist } from '../types';
import { useHistory } from 'react-router';
import { makeNewPlaylist, getPlaylist, savePlaylist } from '../utils/playlist';
import { PlaylistWrap, PlaylistRow } from './PlaylistModal';

import Modal from './Modal';
import Input from './Input';
import auth from '../utils/auth';
import css from '@emotion/css';
import song from '../utils/song';

interface MainSongRowProps {
  songId: number | string;
  id: string;
  rank?: number | string;
  title: string;
  artist: string;
  onClick?: () => any;
  visiblePlaylistButton?: boolean;
}

export default ({
  songId,
  id,
  title,
  artist,
  rank,
  onClick,
  visiblePlaylistButton,
}: MainSongRowProps) => {
  const history = useHistory();
  const [playlists, setPlaylists] = useState<Array<IPlaylist>>([]);
  const [isOpenPlaylistModal, setIsOpenPlaylistModal] = useState(false);
  const [isNewPlaylist, setIsNewPlaylist] = useState(false);
  const [newPlaylistName, setNewPlaylistName] = useState<string>('');

  const PlayListIcon = require(`../assets/songrow-icon-1.svg`).default;

  const handleClick = () => {
    song.setSelectedSong(id);
    history.push(`/notes/${songId}`);
  }

  useEffect(() => {
    if (isOpenPlaylistModal) {
      getPlaylist().then((playlists) => setPlaylists(playlists));
    }
  }, [isOpenPlaylistModal]);

  return (
    <>
      <Container id={id} onClick={onClick || handleClick}>
        {rank ? (
          <Rank>{rank}</Rank>
        ) : (
          <>
            <PlayButtonWrap className="normal">
              <PlayButtonNormal />
            </PlayButtonWrap>
            <PlayButtonWrap className="hover">
              <PlayButtonHover />
            </PlayButtonWrap>
          </>
        )}
        <InfoWrap>
          <Title>{title}</Title>
          <Artist>{artist}</Artist>
          {visiblePlaylistButton && (
            <Button
              onClick={(e: React.MouseEvent) => {
                e.stopPropagation();
                auth.getToken()
                  ? setIsOpenPlaylistModal(true)
                  : history.push('/playlist');
              }}
            >
              <PlayListIcon />
            </Button>
          )}
        </InfoWrap>
      </Container>
      {isOpenPlaylistModal && (
        <Modal
          open={isOpenPlaylistModal}
          onClose={() => setIsOpenPlaylistModal(false)}
          title={!isNewPlaylist ? 'PLAYLIST' : 'NEW PLAYLIST'}
          firstButtonLabel={
            !isNewPlaylist ? '새 플레이리스트 생성' : '생성하기'
          }
          onClickFirstButton={
            isNewPlaylist
              ? async () => {
                  await makeNewPlaylist(newPlaylistName);
                  const playlists = await getPlaylist();
                  setPlaylists(playlists);
                  setIsNewPlaylist(false);
                }
              : () => setIsNewPlaylist(true)
          }
          secondButtonLabel="취소"
          onClickSecondButton={() =>
            isNewPlaylist
              ? setIsNewPlaylist(false)
              : setIsOpenPlaylistModal(false)
          }
          content={
            <>
              {!isNewPlaylist ? (
                <PlaylistWrap>
                  {playlists.map((playlist) => (
                    <PlaylistRow
                      key={`playlist-${playlist.id}`}
                      id={playlist.id}
                      title={playlist.name}
                      count={playlist.total_count}
                      onClick={async (playlistId: number) => {
                        await savePlaylist(playlistId, songId.toString());
                        const playlists = await getPlaylist();
                        setPlaylists(playlists);
                        setIsOpenPlaylistModal(false);
                      }}
                    />
                  ))}
                </PlaylistWrap>
              ) : (
                <StyledInput
                  icon="edit"
                  placeholder="PLAYLIST 이름을 입력해주세요"
                  value={newPlaylistName}
                  onChange={(e) => setNewPlaylistName(e.target.value)}
                />
              )}
            </>
          }
        />
      )}
    </>
  );
};

const Container = styled.div`
  display: flex;
  align-items: center;
  height: 86px;
  max-height: 86px;
  padding: 22px 51px 22px 29px;
  border-bottom: 1px solid rgba(120, 120, 120, 0.18);
  cursor: pointer;

  & .normal {
    display: block;
  }
  & .hover {
    display: none;
  }
  &:hover {
    background-color: #1c2230;
    border-color: #0a62ba;
    border-width: 2px;
    & span {
      color: #0a62ba !important;
      opacity: 1;
    }
    & .normal {
      display: none;
    }
    & .hover {
      display: block;
    }
    html[color-mode='light'] & {
      background-color: rgba(41, 83, 150, 0.05);
    }
  }

  html[color-mode='light'] & div.normal svg path {
    fill: #1e1e1e;
    opacity: 0.7;
  }

  @media screen and (max-width: 500px) {
    padding: 15px;
  }
`;

const PlayButtonWrap = styled.div`
  margin-right: 15px;
`;

const Rank = styled.span`
  min-width: 49px;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.07;
  text-align: center;
  color: #ffffff;
  margin-right: 20px;
  html[color-mode='light'] & {
    color: #1e1e1e;
  }
`;

const Title = styled.span`
  display: block;
  align-items: center;
  width: 100%;
  max-width: 50%;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.07;
  color: #ffffff;
  word-break: keep-all;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-right: 30px;
  html[color-mode='light'] & {
    color: #1e1e1e;
  }
  @media screen and (max-width: 500px) {
    max-width: 50%;
  }
`;

const Artist = styled.span`
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 50%;
  font-size: 13px;
  line-height: 1.23;
  opacity: 0.4;
  color: #ffffff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-right: 30px;
  html[color-mode='light'] & {
    color: #1e1e1e;
  }
  @media screen and (max-width: 500px) {
    max-width: 80%;
  }
`;

const StyledInput = styled(Input)`
  margin-top: 36px;

  html[color-mode='dark'] & {
    background-color: #2e3137;
  }
`;

interface IButton {
  checked?: boolean;
  isOpacityDisabled?: boolean;
}
const Button = styled.button<IButton>`
  border: 0;
  padding: 0;
  outline: 0;
  background: none;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  min-width: 37px;
  min-height: 37px;
  background-color: rgba(74, 74, 75, 0.05);
  border-radius: 6px;
  margin-left: auto;
  &:last-of-type {
    margin-right: 0;
  }

  html[color-mode='dark'] & {
    background-color: rgba(216, 216, 216, 0.09);

    & path {
      /* opacity: ${({ isOpacityDisabled }) =>
    isOpacityDisabled ? 1 : 0.4} !important; */
      fill: #ffffff !important;
      opacity: 0.4;
    }
  }

  ${({ checked }) =>
    checked &&
    css`
      background-color: rgba(10, 98, 186, 0.69);
      html[color-mode='dark'] & {
        background-color: rgba(10, 10, 11, 0.35);
        & path {
          fill: white !important;
          opacity: 0.53;
        }
      }
    `}
`;

const InfoWrap = styled.div`
  width: 100%;
  display: flex;
  @media screen and (max-width: 500px) {
    flex-direction: column;
    & > *:first-of-type {
      margin-bottom: 4px;
    }
  }
`;
