const { parse } = JSON;

interface ISong {
  setSelectedSong(song: string): void;
  getSelectedSong(): string | null;
}

const song: ISong = {
  setSelectedSong(song: string){
    return localStorage.setItem("selectedSong", song);
  },
  getSelectedSong(){
    return localStorage.getItem("selectedSong");
  }
};

export default song;
